import React from 'react';
import Tilt from 'react-tilt';
import PNG from './clipart1249120.png'
import './Logo.css';
const Logo=()=>
{
   return(
    <div className='ma4 mt0'>
    <Tilt className="Tilt br2 shadow-2" options={{ max : 35 }} style={{ height: 150, width: 150 }} >
 <div className="Tilt-inner pa3"> <img style={{ height:130,paddingTop:'0px',paddingLeft:'10px'}} alt='logo ' src={PNG}/></div>
</Tilt>
    </div>
   	)
}

export default Logo;